const createSongLyricList = (songLyric='[00:00.000]  : \\n[00:01.000]  : Lance\\n[00:06.460] \\n[00:07.954]\\n[00:09.712]\\n[00:11.464]\\n[00:13.212]demo\\n')=>{
    const list = []
    const lycArr = songLyric.split('[')
    for (let i = 0; i < lycArr.length; i++) {
        const arr = lycArr[i].split(']')
        // 
        const text = arr[1]
        // 
        const time = arr[0].split('.')
        const timer = time[0].split(':')
        const ms = timer[0]*60 + timer[1] * 1
        if ( text && !isNaN(ms) ){
            list.push({
                ms,text
            })
        }
    }
    
    return list
}


export default createSongLyricList