import api from "@/common/baseProxy"

// 
export function getPlaylistComment(id, limit = 20, offset = 0) {
    return $axios.get(api +  "/comment/playlist", {
        params: {
            id,
            limit,
            offset,
        },
    });
}

// /
/**
 *
 * @param t: 1 , 2 
 * @param type:  ,, 0 ,1 mv,2 ,3 ,4 ,5 ,6 
 * @param id:  id
 * @param content: 
 */
export function sendComment(t=1,type= 2,id= 0,content='test') {
    return $axios.get( api + '/comment?t='+t+'&type='+type+'&id='+id+'&content='+content)
}

export function replyComment(t=2,type=2,id=0,content='test',commentId=0) {
    return $axios.get( api + '/comment?t='+t+'&type='+type+'&id='+id+'&commentId='+commentId+'&content='+content )
}

// 
export function getSongSheetSubscribers(id=0) {
    return $axios.get(api + '/playlist/subscribers?id='+id+'&limit=1000')
}


